.comment-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 1rem .8rem;
}

.comment-item:nth-child(even) {
  background-color: #eee;
}

.comment-item__info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;

  margin-bottom: 1.6rem;
}

.comment-item__name {
  font-size: 1.8rem;
  text-transform: uppercase;
}

.comment-item__email {
  font-size: 1.2rem;
  font-style: italic;
}

.comment-item__body {
  width: 80%;
  font-size: 1.5rem;
  margin: 0 0 1.6rem 1.6rem;
}

.comment-edit-btn {
  -ms-flex-item-align: end;
      align-self: flex-end;
}




